  background-repeat: no-repeat;
  background-size: 12px 12px;
  background-position: right 8px center;
}

.select {
  display: inline-block;
  position: relative;
  overflow: hidden;
  background: #fff;
}

.select select {
  width: 100%;
  padding-right: 24px;
  border: 1px solid #333;
  background: none;
  text-overflow: "";
  cursor: pointer;
}

.select::before {
  position: absolute;
  top: 13px;
  right: 8px;
  width: 0;
  height: 0;
  padding: 0;
  content: "";
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid #666;
  pointer-events: none;
}

/* radio & checkbox */
input[type="radio"],
input[type="checkbox"] {
  display: none;
}

input[type="radio"] + span,
input[type="checkbox"] + span {
  display: inline-block;
  position: relative;
  color: #1B224C;
  cursor: pointer;
}

input[type="radio"] + span::before,
input[type="checkbox"] + span::before {
  position: absolute;
  width: 16px;
  height: 16px;
  content: "";
  border: 1px solid #333;
  background: #fff;
}

/* fieldset */
fieldset {
  padding: 8px 16px;
  border: 1px solid #ccc;
}

legend {
  padding: 0 8px;
}

/* button */
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  display: inline-block;
  margin: 0;
  border-radius: 0;
  -webkit-box-sizing: content-box;
  -webkit-appearance: button;
  appearance: button;
  cursor: pointer;
}

/* ブラウザ対策
---------------------------------------------------------------------------- */
/* Firefox */
/* IE */
select::-ms-expand {
  display: none;
}

/* webkit */
/* iOS */
input[type="submit"]::-webkit-search-decoration,
input[type=reset]::-webkit-search-decoration,
input[type="button"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-decoration {
  display: none;
}

::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

input[type="search"]::-webkit-search-decoration {
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  -moz-appearance: none;
  -webkit-appearance: textfield;
  -webkit-box-sizing: border-box;
  appearance: none;
  background: none;
  outline: none;
  font-size: 16px;
}

/* 挙動
---------------------------------------------------------------------------- */
/* hover */
input:hover,
textarea:hover,
select:hover {
  border-color: #666;
}

input[type="radio"] + span:hover,
input[type="checkbox"] + span:hover {
  color: #000;
}

input[type="radio"] + span:hover::before,
input[type="checkbox"] + span:hover::before {
  border-color: #000;
}

input[type="radio"] + span:hover::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 4px;
  width: 8px;
  height: 8px;
  background: #333;
}

/* focus */
input:focus,
textarea:focus {
  border-color: #3498db;
}

input[type="submit"]:focus,
input[type=reset]:focus,
input[type="button"]:focus,
input[type="search"]:focus {
  outline-offset: -2px;
}

/* disabled */
input:disabled,
input:disabled:hover,
textarea:disabled,
textarea:disabled:hover {
  border-color: #ccc;
  background: #eee;
  cursor: not-allowed;
}

input[type="radio"]:disabled + span,
input[type="checkbox"]:disabled + span {
  color: #ccc;
  cursor: not-allowed;
}

input[type="radio"]:disabled + span::before,
input[type="checkbox"]:disabled + span::before {
  border-color: #ccc;
  cursor: not-allowed;
}

/* バリデーション */
/* placeholder */
*,
*::before,
*::after {
  box-sizing: border-box;
}

